.address .container {
  padding: 120px 20px;

  @include vp-1023 {
    padding: 100px 45px 101px 45px;
  }

  @include vp-767 {
    padding: 12px 15px 64px 15px;
  }
}

.address__wrapper {
  display: grid;
  grid-template-columns: 560px 1fr;
  column-gap: 100px;

  @include vp-1023 {
    grid-template-columns: 1fr 1fr;
    column-gap: 30px;
  }

  @include vp-767 {
    display: flex;
    flex-direction: column-reverse;
  }
}

.address__map-wrapper {
  position: relative;
}

.address__map-wrapper img {
  @include vp-1023 {
    width: 100%;
    height: 335px;
    object-fit: cover;
  }

  @include vp-767 {
    object-fit: cover;
    height: 316px;
  }
}

.address__map {
  position: absolute;
  top: 0;
  left: 0;
  border: none;

  @include vp-1023 {
    grid-template-columns: 324px 1fr;
    width: 100%;
    height: 335px;
  }

  @include vp-767 {
    width: 100%;
    height: 316px;
  }
}

.address__map-marker {
  position: absolute;
  top: 249px;
  left: 217px;

  @include vp-767 {
    top: 224px;
    left: 112px;
  }
}

.address__info-wrapper {
  display: flex;
  flex-direction: column;
  padding: 75px 0 0 0;

  @include vp-1023 {
    padding: 11px 0 0 0;
    align-self: center;
  }

  @include vp-767 {
    padding: 0;
  }
}

.address__info {
  @include vp-1023 {
    width: 88%;
  }

  @include vp-767 {
    width: 100%;
  }
}

.address__info-wrapper p {
  @include font(18px, 150%, 500);

  margin: 0 0 32px 0;

  @include vp-1023 {
    margin: 0 0 29px 0;
  }

  @include vp-767 {
    margin: 0 0 27px 0;
  }
}

.address__info span {
  @include font(16px, 150%, 400);

  display: block;
  font-style: normal;
  color: $primary-color;
  margin: 0 0 22px 0;
  padding-left: 28px;
  position: relative;

  &::before {
    content: "";
    position: absolute;
    top: 5px;
    left: 0;
    width: 18px;
    height: 22px;
    background: url("../img/svg/marker.svg") no-repeat center;
  }

  @include vp-1023 {
    margin: 0 0 20px 0;
  }

  @include vp-767 {
    margin: 0 0 21px 0;
  }
}

.address__info a {
  @include font(16px, 150%, 400);

  cursor: pointer;
  display: inline-block;
  vertical-align: baseline;
  font-style: normal;
  color: $primary-color;
  padding-left: 28px;
  position: relative;

  &::before {
    content: "";
    position: absolute;
    top: 2px;
    left: 0;
    width: 18px;
    height: 18px;
    background: url("../img/svg/phone.svg") no-repeat center;
  }

  @include vp-1023 {
    margin: 0 0 12px 0;
  }

  @include vp-767 {
    margin: 0 0 36px 0;
  }

  @include hover {
    color: #008cff;
  }

  @include active {
    color: #005eac;
  }

  @include focus {
    outline: 1px solid #0161b0;
  }
}
