.form {
  width: 403px;
  background-color: $primary-color;
  box-shadow: 0 4px 45px rgba(1, 28, 64, 0.15);
  padding: 44px 50px 50px 50px;

  @include vp-1023 {
    margin: 0 auto 100px;
    padding: 43px 50px 50px 50px;
  }

  @include vp-767 {
    padding: 36px 30px 40px 30px;
    margin: 0;
    width: 100%;
  }
}

.form h2 {
  @include font(18px, 150%, 500);

  margin: 0 0 27px 0;
  color: $second-color;

  @include vp-1023 {
    margin: 0 0 28px 0;
  }

  @include vp-767 {
    @include font(18px, 140%, 500);
    text-align: center;
    margin: 0 0 27px 0;
  }
}

.form__input input {
  width: 100%;
  border: none;
  padding: 11px 0 11px 16px;
  outline: none;
  color: $primary-color;

  &::placeholder {
    color: rgba(1, 28, 64, 0.7);
  }

  @include font(14px, 150%, 400);

  @include hover {
    outline: 2px solid #6fe0fc;
  }

  @include focus {
    outline: 2px solid #42d2f6;
  }

  @include active {
    outline: 2px solid #53a9be;
  }
}

.form__input {
  margin-bottom: 20px;

  @include focus {
    outline: 2px solid #88c7f7;
  }
}

//checkbox-custom

.form__check {
  margin: 0 0 30px 0;

  @include vp-767 {
    margin: 0 0 12px 0;
  }
}

.form__check label {
  display: block;
  padding-left: 24px;
  cursor: pointer;
}

.form__check input {
  appearance: none;
  position: absolute;
}

.form__check input:checked + span {
  background: url("../img/svg/check.svg") no-repeat center, $second-color;
}

.form__check input:hover + span {
  outline: 2px solid #3aa5f6;
}

.form__check input:focus + span {
  outline: 2px solid #68bdff;
}

.form__check input:active + span {
  outline: 2px solid #4a8dc1;
}

.form__check span {
  margin-left: -24px;
  position: absolute;
  width: 18px;
  height: 18px;
  background-color: $second-color;

  @include vp-767 {
    width: 20px;
    height: 20px;
  }
}

.form__check-label {
  display: contents;

  @include font(10px, 130%, 400);

  position: relative;
  top: 2px;
  margin: 0;
  color: $second-color;

  @include vp-767 {
    top: -2px;
    display: block;
  }
}

.form__button-submit {
  @include font(14px, 150%, 600);

  cursor: pointer;
  border: none;
  outline: none;
  width: 100%;
  padding: 10px 0 11px;
  background: $second-color;

  @include vp-1023 {
    padding: 9px 0 11px;
  }

  @include vp-767 {
    padding: 11px 0 10px;
    margin: 51px 0 0 0;
  }

  @include hover {
    background: #6cbefd;
    color: $second-color;
  }

  @include active {
    background: #42a6f3;
  }

  @include focus {
    outline: 2px solid #42d2f6;
  }

  &:disabled {
    background: $second-color;
  }
}
